.component-sankey {
  position:relative;
  z-index:2;
  &.drop-hover {
    z-index:-1;
  }
  rect {
    stroke:$light;
    stroke-width:1px;
  }
  &:hover {
    .node,.link {
      transition:all .5s ease-out;
      opacity:.2;
      &.highlight {
        opacity:1;
      }
    }
  }
  .node {
    &:hover {
      svg {
        pointer-events:all;
        opacity:.8;
      }
    }
    svg {
      transition:all .3s ease;
      opacity:0;
      pointer-events:none;
      color:$light;
      cursor:pointer;
      font-size:em(18);
      &:hover {
        opacity:1;
      }
    }
  }
  .agency-grouping {
    cursor:pointer;
    &:hover {
      rect {
        fill:$greenDark;
        stroke:$greenDark;
      }
      text {
        fill:$light;
      }
    }
    rect {
      fill:$light;
      stroke:$dark;
      stroke-width:1px;
    }
    text {
      font-size:12px;
      text-anchor: middle;
      alignment-baseline: middle;
      dominant-baseline: central;
    }
  }
}

.App {
  &.has_active-1 {
    g.links {
      display:none;
    }
    .node-well-1 {
      display:none;
    }
  }
}